/**
 * è°ƒèµ·å¾®ä¿¡æ”¯ä»˜
 * @param params å¾®ä¿¡æ”¯ä»˜å‚æ•°
 * @returns Promise<void>
 */
export const launchWechatPay = (params: {
  appId: string;
  timeStamp: string;
  nonceStr: string;
  package: string;
  signType: string;
  paySign: string;
}): Promise<void> => {
  return new Promise((resolve, reject) => {
    // æ£€æŸ¥æ˜¯å¦åœ¨å¾®ä¿¡ç¯å¢ƒ
    if (typeof window === 'undefined' || !(window as any).WeixinJSBridge) {
      // å¦‚æœä¸åœ¨å¾®ä¿¡ç¯å¢ƒï¼Œå°è¯•ä½¿ç”¨ JSSDK
      if ((window as any).wx) {
        // ä½¿ç”¨å¾®ä¿¡ JSSDK è°ƒèµ·æ”¯ä»˜
        (window as any).wx.chooseWXPay({
          timestamp: params.timeStamp,
          nonceStr: params.nonceStr,
          package: params.package,
          signType: params.signType,
          paySign: params.paySign,
          success: () => {
            resolve();
          },
          fail: (err: any) => {
            reject(new Error(err.errMsg || 'æ”¯ä»˜å¤±è´¥'));
          },
        });
      } else {
        reject(new Error('è¯·åœ¨å¾®ä¿¡ä¸­æ‰“å¼€'));
      }
      return;
    }

    // ä½¿ç”¨ WeixinJSBridge è°ƒèµ·æ”¯ä»˜
    (window as any).WeixinJSBridge.invoke(
      'getBrandWCPayRequest',
      {
        appId: params.appId,
        timeStamp: params.timeStamp,
        nonceStr: params.nonceStr,
        package: params.package,
        signType: params.signType,
        paySign: params.paySign,
      },
      (res: any) => {
        if (res.err_msg === 'get_brand_wcpay_request:ok') {
          resolve();
        } else {
          reject(new Error(res.err_msg || 'æ”¯ä»˜å¤±è´¥'));
        }
      }
    );
  });
};

/**
 * è°ƒèµ·æ”¯ä»˜å®æ”¯ä»˜
 * @param orderString æ”¯ä»˜å®è®¢å•å­—ç¬¦ä¸²
 * @returns Promise<void>
 */
export const launchAlipay = (orderString: string): Promise<void> => {
  return new Promise((resolve, reject) => {
    // åˆ›å»ºéšè—çš„è¡¨å•å¹¶æäº¤
    const form = document.createElement('form');
    form.method = 'POST';
    form.action = 'https://openapi.alipay.com/gateway.do';
    form.style.display = 'none';

    // è§£æ orderString å¹¶åˆ›å»ºè¡¨å•å­—æ®µ
    // orderString æ ¼å¼é€šå¸¸æ˜¯ï¼šapp_id=xxx&biz_content=xxx&charset=xxx&...
    const params = new URLSearchParams(orderString);
    params.forEach((value, key) => {
      const input = document.createElement('input');
      input.type = 'hidden';
      input.name = key;
      input.value = value;
      form.appendChild(input);
    });

    document.body.appendChild(form);
    form.submit();

    // æ³¨æ„ï¼šæ”¯ä»˜å®æ”¯ä»˜åä¼šåœ¨æ–°çª—å£æ‰“å¼€ï¼Œæ— æ³•ç›´æ¥ç›‘å¬ç»“æœ
    // éœ€è¦é€šè¿‡è½®è¯¢è®¢å•çŠ¶æ€æ¥ç¡®è®¤æ”¯ä»˜ç»“æœ
    resolve();
  });
};

/**
 * è½®è¯¢è®¢å•çŠ¶æ€ç›´åˆ°æ”¯ä»˜å®Œæˆæˆ–è¶…æ—¶
 * @param orderId è®¢å•ID
 * @param maxAttempts æœ€å¤§å°è¯•æ¬¡æ•°
 * @param interval è½®è¯¢é—´éš”ï¼ˆæ¯«ç§’ï¼‰
 * @returns è®¢å•çŠ¶æ€
 */
export const pollOrderStatus = async (
  orderId: string,
  maxAttempts = 30,
  interval = 2000
): Promise<PaymentOrder> => {
  for (let i = 0; i < maxAttempts; i++) {
    try {
      const order = await queryOrderStatus(orderId);
      if (order.status === 'SUCCESS' || order.status === 'FAILED' || order.status === 'CANCELLED') {
        return order;
      }
      // ç­‰å¾…åç»§ç»­è½®è¯¢
      await new Promise((resolve) => setTimeout(resolve, interval));
    } catch (error) {
      console.error(`Poll attempt ${i + 1} failed:`, error);
      if (i === maxAttempts - 1) {
        throw error;
      }
      await new Promise((resolve) => setTimeout(resolve, interval));
    }
  }
  throw new Error('æ”¯ä»˜è¶…æ—¶ï¼Œè¯·ç¨åæŸ¥è¯¢è®¢å•çŠ¶æ€');
};

