import { getCookie, setCookie, deleteCookie } from 'cookies-next';

// Cookie é”®å
const USER_ID_KEY = 'ip_creative_user_id';

// Cookie é€‰é¡¹ï¼ˆ30å¤©è¿‡æœŸï¼‰
const COOKIE_OPTIONS = {
  maxAge: 60 * 60 * 24 * 30, // 30å¤©
  path: '/',
  sameSite: 'lax' as const,
} as const;

/**
 * ä¿å­˜ç”¨æˆ·IDåˆ° cookie
 */
export const setUserId = (userId: string) => {
  setCookie(USER_ID_KEY, userId, COOKIE_OPTIONS);
};

/**
 * ä» cookie è·å–ç”¨æˆ·ID
 */
export const getUserId = (): string | null => {
  const userId = getCookie(USER_ID_KEY);
  return userId ? String(userId) : null;
};

/**
 * æ¸…é™¤ç”¨æˆ·ID cookie
 */
export const clearUserId = () => {
  deleteCookie(USER_ID_KEY);
};

/**
 * æ£€æŸ¥ç”¨æˆ·æ˜¯å¦å·²ç™»å½•ï¼ˆé€šè¿‡ cookieï¼‰
 */
export const isAuthenticated = (): boolean => {
  return !!getUserId();
};

