// API åŸºç¡€è·¯å¾„
const API_BASE_URL = "/api/cloud";

/**
 * è°ƒç”¨äº‘å‡½æ•°ï¼ˆé€šè¿‡ Next.js API è·¯ç”±ï¼‰
 * @param name äº‘å‡½æ•°åç§°
 * @param data ä¼ é€’ç»™äº‘å‡½æ•°çš„æ•°æ®
 * @returns äº‘å‡½æ•°è¿”å›ç»“æœ
 */
export const callCloudFunction = async (
  name: string,
  data: any = {}
): Promise<any> => {
  try {
    const response = await fetch(`${API_BASE_URL}/${name}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    if (!response.ok) {
      const error = await response.json();
      return {
        success: 0,
        message: error.message || `HTTP è¯·æ±‚å¤±è´¥: ${response.status}`,
      };
    }

    const result = await response.json();

    // å¦‚æœ API è¿”å›çš„ success ä¸æ˜¯ 1ï¼Œè¿”å›å¤±è´¥ç»“æœ
    if (result.success !== 1) {
      return {
        success: 0,
        message: result.message || "è°ƒç”¨äº‘å‡½æ•°å¤±è´¥",
      };
    }

    // æˆåŠŸæ—¶è¿”å›åŒ…å« success: 1 çš„ç»“æœ
    return {
      success: 1,
      ...result,
    };
  } catch (error: any) {
    console.error(`è°ƒç”¨äº‘å‡½æ•° ${name} å¤±è´¥:`, error);
    return {
      success: 0,
      message: error.message || `è°ƒç”¨äº‘å‡½æ•° ${name} å¤±è´¥`,
    };
  }
};
