/**
 * è·å–ç”¨æˆ·ä¿¡æ¯ï¼ˆåŒ…æ‹¬ä½¿ç”¨æ¬¡æ•°ï¼‰
 * @param userId ç”¨æˆ·IDï¼ˆå¯é€‰ï¼Œå¦‚æœä¸æä¾›åˆ™ä» localStorage æˆ– cookie è·å–ï¼‰
 * @returns ç”¨æˆ·ä¿¡æ¯
 */
export const getUserInfo = async (userId?: string): Promise<UserInfo> => {
  try {
    // å¦‚æœæ²¡æœ‰æä¾› userIdï¼Œå°è¯•ä» cookie è·å–
    const storedUserId = userId || getUserId();

    if (!storedUserId) {
      return {
        userTier: UserTier.FREE,
        sceneUsage: { date: new Date().toLocaleDateString(), count: 0 },
        memeUsage: { date: new Date().toLocaleDateString(), count: 0 },
      };
    }

    const result = await callCloudFunction("user", {
      action: "getInfo",
      userId: storedUserId,
    });

    // å¤„ç†äº‘å‡½æ•°è¿”å›ç»“æœï¼ˆAPI è·¯ç”±ç›´æ¥è¿”å› { success, data }ï¼‰
    const response = result;

    if (response.success !== 1) {
      if (response.message === "ç”¨æˆ·ä¸å­˜åœ¨") {
        return {
          userTier: UserTier.FREE,
          sceneUsage: { date: new Date().toLocaleDateString(), count: 0 },
          memeUsage: { date: new Date().toLocaleDateString(), count: 0 },
        };
      }
      throw new Error(response.message || "è·å–ç”¨æˆ·ä¿¡æ¯å¤±è´¥");
    }

    const data = response.data;

    return {
      userId: data.userId,
      phone: data.phone,
      userTier: data.userTier || UserTier.FREE,
      sceneUsage: data.sceneUsage || {
        date: new Date().toLocaleDateString(),
        count: 0,
      },
      memeUsage: data.memeUsage || {
        date: new Date().toLocaleDateString(),
        count: 0,
      },
    };
  } catch (error) {
    console.error("Get user info error:", error);
    // è¿”å›é»˜è®¤å€¼è€Œä¸æ˜¯æŠ›å‡ºé”™è¯¯ï¼Œç¡®ä¿åº”ç”¨å¯ä»¥ç»§ç»­è¿è¡Œ
    return {
      userTier: UserTier.FREE,
      sceneUsage: { date: new Date().toLocaleDateString(), count: 0 },
      memeUsage: { date: new Date().toLocaleDateString(), count: 0 },
    };
  }
};

